@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin telefonoXl {
    @media (min-width: v.$telefonoXl) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin tabletXl {
    @media (min-width: v.$tabletLg) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXl {
    @media (min-width: v.$desktopXl) {
        @content;
    }
}


@mixin boton($Gcolor, $Ccolor, $texto) {
    background: linear-gradient(to right, $Gcolor 30%, $Ccolor 100%);
    font-size: 1.6rem;
    padding: 1.2rem 3rem;
    font-weight: 700;
    text-align: center;
    color: $texto;
    text-decoration: none;
    border: none;
    border-radius: 3rem;

    &:hover {
        cursor: pointer;
        background: linear-gradient(to right, lighten($Gcolor, 10%) 30%, lighten($Ccolor, 10%) 100%);
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}
