@use 'base/variables' as v;
@use 'base/mixins' as m;

.hero {
    background-color: v.$veryLightGray;

    &__contenido {
        @include m.desktop {
            display: flex;
            align-items: center;
        }
    }

    &__imagen-c {
        background-image: url(../img/bg-intro-mobile.svg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        min-height: 27.5rem;
        position: relative;
        
        @include m.telefonoXl {
            min-height: 34.5rem;
            background-position: center 70%;
        }

        @include m.tablet {
            min-height: 40rem;
        }

        @include m.desktop { 
            flex: 3;
            order: 2;
            min-height: 60rem;
            background-image: none;
            // background-image: url(../img/bg-intro-desktop.svg);
            // background-position: left center;
        }

        
        &::before {
            @include m.desktop { 
                content: '';
                background-image: url(../img/bg-intro-desktop.svg);
                background-size: 122%;
                background-repeat: no-repeat;
                background-position: center bottom;
                position: absolute;
                width: 150%;
                height: 100%;
                background-position: 0% 63%;
                // left: 50%;
                // transform: translateX(-50%);   
            }
        }

        &::after {
            content: '';
            background-image: url(../img/image-mockups.png);
            background-size: 93%;
            background-repeat: no-repeat;
            background-position: center bottom;
            position: absolute;
            width: 100%;
            height: 100%;
            // left: 50%;
            // transform: translateX(-50%);
            animation-name: imagen-hero;
            animation-duration: 1s;
            animation-timing-function: ease;

            @include m.telefonoXl {
                background-size: 55%;
            }

            @include m.tablet {
                background-size: 52%;
            }

            @include m.desktop {
                width: 120%;
                height: 145%;
                background-size: 100%;
                background-position: center -12rem;
                left: 22%;
            }

            @include m.desktopXl {
                background-size: 85%;
            }
            
        }
    }

    &__info {
        text-align: center;

        @include m.telefonoXl {
            margin: 0 auto;
            width: 85%;
        }

        @include m.desktop { 
            width: 100%;
            margin-top: 0;
            flex: 2;
            order: 1;
            text-align: left;
        }
    }

    &__texto {
        font-weight: 300;
    }

    &__enlace {
        @include m.boton(v.$limeGreen, v.$brightCyan, v.$white);
        margin-top: 3rem;
        display: inline-block;
    }
}