@use 'base/mixins' as m;
@use 'base/variables' as v;

.features {
    background-color: v.$lightGrayishBlue;

    &__c {
        text-align: center;

        @include m.telefonoXl {
            width: 70%;
            margin: 0 auto;
        }

        @include m.tablet {
            width: 100%;
            text-align: left;
        }
    }

    &__texto {
        @include m.desktop {
            width: 50%;
        }
    }

    &__g {
        margin-top: 8rem;
        display: grid;
        @include m.grid(1, 5rem);

        @include m.tablet {
            @include m.grid(2, 5rem);
        }

        @include m.desktop {
            @include m.grid(4, 4rem);
        }
    }
}

.feature {

    &:nth-child(1) h3,
    &:nth-child(2) h3,
    &:nth-child(3) h3,
    &:nth-child(4) h3 {

        &::before {
            display: block;
            content: '';
            background-image: url(../img/icon-online.svg);
            background-repeat: no-repeat;
            background-size: 6rem;
            width: 6rem;
            height: 6rem;
            margin: 0 auto 2rem auto;

            animation-name: updown;
            animation-duration: 3s;
            animation-timing-function: ease;
            animation-iteration-count: infinite;

            @include m.tablet { 
                margin: 0 0 2rem 0;
            }
        }
    }

    &:nth-child(2) h3 {
        &::before {
            background-image: url(../img/icon-budgeting.svg);
        }
    }

    &:nth-child(3) h3 {
        &::before {
            background-image: url(../img/icon-onboarding.svg);
        }
    }

    &:nth-child(2) h3 {
        &::before {
            background-image: url(../img/icon-api.svg);
        }
    }
}

