@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    box-shadow: 0px 3px 12px rgb(0 0 0 / 15%);
    background-color: v.$white;

    &__barra {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 3rem;
        position: relative;
        z-index: 1;
        background-color: v.$white;

        @include m.tablet {
            flex-direction: row;
        }
    }

    &__invite {
        display: none;
        
        @include m.tablet {
            display: block;
        }
        @include m.boton(v.$limeGreen, v.$brightCyan, v.$white);
    }

    &__button {
        background-image: url(../img/icon-hamburger.svg);
        background-repeat: no-repeat;
        background-size: 2rem;
        background-position: center;
        width: 2.5rem;
        height: 2.5rem;

        &[aria-expanded="true"] {
            background-image: url(../img/icon-close.svg);
        }

        @include m.tablet { 
            display: none;
        }
    }
}

.nav {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 3rem;
    position: fixed;
    width: 90%;
    top: 15%;
    left: 50%;
    right: 0;
    transform: translateX(58%);
    background-color: v.$white;
    padding: 4rem 2rem;
    border-radius: .5rem;
    box-shadow: 0 5px 10px v.$grayishBlue;
    z-index: 1000;

    transition-property: transform;
    transition-duration: .4s;

    &[data-visible="true"] {
        transform: translateX(-50%);

        @include m.tablet { 
            transform: translateX(0%);
        }
    }

    @include m.tablet { 
        flex-direction: row;
        position: unset;
        width: auto;
        background-color: transparent;
        transform: translateX(0%);
        padding: 0;
        box-shadow: none;
    }

    @include m.desktop {
        gap: 3rem;
    }

    &__enlace {
        color: v.$darkBlue;
        font-weight: bold;

        @include m.tablet {
            position: relative;
            display: block;
            color: v.$grayishBlue;
            font-weight: normal;

            transition-property: color;
            transition-duration: .3s;

            &:hover {
                color: v.$darkBlue
            }
        }

        &::before {
            @include m.desktop { 
                content: '';
                background: linear-gradient(to right, v.$limeGreen 30%, v.$brightCyan 100%);
                width: 100%;
                height: .5rem;
                position: absolute;
                bottom: -3.3rem;
                left: 0;
                opacity: 0;
                transition-property: opacity;
                transition-duration: .3s;   
            }
        }

        &:hover::before {
            @include m.desktop {
                opacity: 1;
            }
        }
    }
}

.overlay {
    opacity: 0;
    position: fixed;
    background-image: linear-gradient(v.$darkBlue, transparent);
  }

.overlay-visible {
    opacity: 1;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;

    @include m.tablet { 
        position: unset;
    }
}