@use 'variables' as v; 
@use 'mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$font;
    font-size: 1.5rem;
    color: v.$grayishBlue;
    font-weight: 300;
    overflow-x: hidden;

    @include m.tablet {
        font-size: 1.6rem;
    }
}

.no-scroll {
    overflow: hidden;

    @include m.tablet { 
        overflow-x: hidden;
        overflow-y: auto;
    }
}

p {
    line-height: 1.5;
    margin: 0;
}

.contenedor {
    max-width: 1200px;
    margin: 0 auto;

    &--pn {
        padding-left: 2rem;
        padding-right: 2rem;
        padding-top: 5rem;
        padding-bottom: 5em;

        @include m.desktop { 
            padding-top: 6.5rem;
            padding-bottom: 6.5rem;
        }
    }

    &--ps {
        padding: 2rem;
    }
}

a {
    text-decoration: none;
    font-weight: 300;
    color: v.$grayishBlue;
    font-size: 1.6rem;
}

img {
    max-width: 100%;
    display: block;
}

h1, h2, h3, h4 {
    margin: 0 0 2rem  0;
    font-family: v.$font;
    color: v.$darkBlue;
    font-weight: 300;

}
h1 {
    font-size: 4rem;

    @include m.desktop {
        font-size: 5rem;
    }
}

h2 {
    font-size: 3.5rem;
}

h3 {
    font-size: 2.5rem;
}

h4 {
    font-size: 2rem;
}

ul {
    padding: 0;
    list-style: none;
    margin: 0;
}

button {
    border: 0;
    background-color: transparent;
    margin: 0;
    padding: 0;
    cursor: pointer;
}

