/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Public Sans", sans-serif;
  font-size: 1.5rem;
  color: hsl(233deg, 8%, 62%);
  font-weight: 300;
  overflow-x: hidden;
}
@media (min-width: 768px) {
  body {
    font-size: 1.6rem;
  }
}

.no-scroll {
  overflow: hidden;
}
@media (min-width: 768px) {
  .no-scroll {
    overflow-x: hidden;
    overflow-y: auto;
  }
}

p {
  line-height: 1.5;
  margin: 0;
}

.contenedor {
  max-width: 1200px;
  margin: 0 auto;
}
.contenedor--pn {
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 5rem;
  padding-bottom: 5em;
}
@media (min-width: 1024px) {
  .contenedor--pn {
    padding-top: 6.5rem;
    padding-bottom: 6.5rem;
  }
}
.contenedor--ps {
  padding: 2rem;
}

a {
  text-decoration: none;
  font-weight: 300;
  color: hsl(233deg, 8%, 62%);
  font-size: 1.6rem;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  margin: 0 0 2rem 0;
  font-family: "Public Sans", sans-serif;
  color: hsl(233deg, 26%, 24%);
  font-weight: 300;
}

h1 {
  font-size: 4rem;
}
@media (min-width: 1024px) {
  h1 {
    font-size: 5rem;
  }
}

h2 {
  font-size: 3.5rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 2rem;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

button {
  border: 0;
  background-color: transparent;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

@keyframes updown {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-2rem);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes imagen-hero {
  0% {
    transform: translateX(-10rem);
    opacity: 0;
  }
  50% {
    transform: translateX(10rem);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
.header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.15);
  background-color: #fff;
}
.header__barra {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3rem;
  position: relative;
  z-index: 1;
  background-color: #fff;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
  }
}
.header__invite {
  display: none;
  background: linear-gradient(to right, hsl(136deg, 65%, 51%) 30%, hsl(192deg, 70%, 51%) 100%);
  font-size: 1.6rem;
  padding: 1.2rem 3rem;
  font-weight: 700;
  text-align: center;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 3rem;
}
@media (min-width: 768px) {
  .header__invite {
    display: block;
  }
}
.header__invite:hover {
  cursor: pointer;
  background: linear-gradient(to right, #5bdc7d 30%, #56c5e1 100%);
}
.header__button {
  background-image: url(../img/icon-hamburger.svg);
  background-repeat: no-repeat;
  background-size: 2rem;
  background-position: center;
  width: 2.5rem;
  height: 2.5rem;
}
.header__button[aria-expanded=true] {
  background-image: url(../img/icon-close.svg);
}
@media (min-width: 768px) {
  .header__button {
    display: none;
  }
}

.nav {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 3rem;
  position: fixed;
  width: 90%;
  top: 15%;
  left: 50%;
  right: 0;
  transform: translateX(58%);
  background-color: #fff;
  padding: 4rem 2rem;
  border-radius: 0.5rem;
  box-shadow: 0 5px 10px hsl(233deg, 8%, 62%);
  z-index: 1000;
  transition-property: transform;
  transition-duration: 0.4s;
}
.nav[data-visible=true] {
  transform: translateX(-50%);
}
@media (min-width: 768px) {
  .nav[data-visible=true] {
    transform: translateX(0%);
  }
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    position: unset;
    width: auto;
    background-color: transparent;
    transform: translateX(0%);
    padding: 0;
    box-shadow: none;
  }
}
@media (min-width: 1024px) {
  .nav {
    gap: 3rem;
  }
}
.nav__enlace {
  color: hsl(233deg, 26%, 24%);
  font-weight: bold;
}
@media (min-width: 768px) {
  .nav__enlace {
    position: relative;
    display: block;
    color: hsl(233deg, 8%, 62%);
    font-weight: normal;
    transition-property: color;
    transition-duration: 0.3s;
  }
  .nav__enlace:hover {
    color: hsl(233deg, 26%, 24%);
  }
}
@media (min-width: 1024px) {
  .nav__enlace::before {
    content: "";
    background: linear-gradient(to right, hsl(136deg, 65%, 51%) 30%, hsl(192deg, 70%, 51%) 100%);
    width: 100%;
    height: 0.5rem;
    position: absolute;
    bottom: -3.3rem;
    left: 0;
    opacity: 0;
    transition-property: opacity;
    transition-duration: 0.3s;
  }
}
@media (min-width: 1024px) {
  .nav__enlace:hover::before {
    opacity: 1;
  }
}

.overlay {
  opacity: 0;
  position: fixed;
  background-image: linear-gradient(hsl(233deg, 26%, 24%), transparent);
}

.overlay-visible {
  opacity: 1;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}
@media (min-width: 768px) {
  .overlay-visible {
    position: unset;
  }
}

.hero {
  background-color: hsl(0deg, 0%, 98%);
}
@media (min-width: 1024px) {
  .hero__contenido {
    display: flex;
    align-items: center;
  }
}
.hero__imagen-c {
  background-image: url(../img/bg-intro-mobile.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 27.5rem;
  position: relative;
}
@media (min-width: 567px) {
  .hero__imagen-c {
    min-height: 34.5rem;
    background-position: center 70%;
  }
}
@media (min-width: 768px) {
  .hero__imagen-c {
    min-height: 40rem;
  }
}
@media (min-width: 1024px) {
  .hero__imagen-c {
    flex: 3;
    order: 2;
    min-height: 60rem;
    background-image: none;
  }
}
@media (min-width: 1024px) {
  .hero__imagen-c::before {
    content: "";
    background-image: url(../img/bg-intro-desktop.svg);
    background-size: 122%;
    background-repeat: no-repeat;
    background-position: center bottom;
    position: absolute;
    width: 150%;
    height: 100%;
    background-position: 0% 63%;
  }
}
.hero__imagen-c::after {
  content: "";
  background-image: url(../img/image-mockups.png);
  background-size: 93%;
  background-repeat: no-repeat;
  background-position: center bottom;
  position: absolute;
  width: 100%;
  height: 100%;
  animation-name: imagen-hero;
  animation-duration: 1s;
  animation-timing-function: ease;
}
@media (min-width: 567px) {
  .hero__imagen-c::after {
    background-size: 55%;
  }
}
@media (min-width: 768px) {
  .hero__imagen-c::after {
    background-size: 52%;
  }
}
@media (min-width: 1024px) {
  .hero__imagen-c::after {
    width: 120%;
    height: 145%;
    background-size: 100%;
    background-position: center -12rem;
    left: 22%;
  }
}
@media (min-width: 1200px) {
  .hero__imagen-c::after {
    background-size: 85%;
  }
}
.hero__info {
  text-align: center;
}
@media (min-width: 567px) {
  .hero__info {
    margin: 0 auto;
    width: 85%;
  }
}
@media (min-width: 1024px) {
  .hero__info {
    width: 100%;
    margin-top: 0;
    flex: 2;
    order: 1;
    text-align: left;
  }
}
.hero__texto {
  font-weight: 300;
}
.hero__enlace {
  background: linear-gradient(to right, hsl(136deg, 65%, 51%) 30%, hsl(192deg, 70%, 51%) 100%);
  font-size: 1.6rem;
  padding: 1.2rem 3rem;
  font-weight: 700;
  text-align: center;
  color: #fff;
  text-decoration: none;
  border: none;
  border-radius: 3rem;
  margin-top: 3rem;
  display: inline-block;
}
.hero__enlace:hover {
  cursor: pointer;
  background: linear-gradient(to right, #5bdc7d 30%, #56c5e1 100%);
}

.features {
  background-color: hsl(220deg, 16%, 96%);
}
.features__c {
  text-align: center;
}
@media (min-width: 567px) {
  .features__c {
    width: 70%;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .features__c {
    width: 100%;
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .features__texto {
    width: 50%;
  }
}
.features__g {
  margin-top: 8rem;
  display: grid;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .features__g {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .features__g {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.feature:nth-child(1) h3::before, .feature:nth-child(2) h3::before, .feature:nth-child(3) h3::before, .feature:nth-child(4) h3::before {
  display: block;
  content: "";
  background-image: url(../img/icon-online.svg);
  background-repeat: no-repeat;
  background-size: 6rem;
  width: 6rem;
  height: 6rem;
  margin: 0 auto 2rem auto;
  animation-name: updown;
  animation-duration: 3s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}
@media (min-width: 768px) {
  .feature:nth-child(1) h3::before, .feature:nth-child(2) h3::before, .feature:nth-child(3) h3::before, .feature:nth-child(4) h3::before {
    margin: 0 0 2rem 0;
  }
}
.feature:nth-child(2) h3::before {
  background-image: url(../img/icon-budgeting.svg);
}
.feature:nth-child(3) h3::before {
  background-image: url(../img/icon-onboarding.svg);
}
.feature:nth-child(2) h3::before {
  background-image: url(../img/icon-api.svg);
}

.articles {
