@use 'base/variables' as v;
@use 'base/mixins' as m;

.articles {
    background-color: v.$veryLightGray;

    &__contenido {
        text-align: center;

        @include m.tablet {
            text-align: left;
        }
    }

    &__f {
        margin-top: 5rem;
        display: flex;
        flex-direction: column;
        gap: 4rem;

        @include m.telefonoXl {
            width: 65%;
            margin: 5rem auto 0 auto;
        }

        @include m.tablet {
            width: 100%;
            @include m.grid(2, 3rem);
        }

        @include m.desktop {
            @include m.grid(4, 3rem);
        }
    }

}


.article {
    border-radius: 1rem;
    overflow: hidden;
    box-shadow: 0px 3px 12px rgb(0 0 0 / 15%);

    &__imagen-c {
        overflow: hidden;
    }

    &__imagen {
        background-image: url(../img/image-currency.jpg);
        background-position: center;
        background-size: cover;
        height: 20rem;

        transition-property: transform;
        transition-duration: .3s;
        transition-timing-function: ease-out;

        &:hover {
            transform: scale(1.2);
        }
    }

    &:nth-child(2) .article__imagen {
        background-image: url(../img/image-restaurant.jpg);
    }

    &:nth-child(3) .article__imagen {
        background-image: url(../img/image-plane.jpg);
    }

    &:nth-child(4) .article__imagen {
        background-image: url(../img/image-confetti.jpg);
    }

    &__contenido {
        padding: 2.5rem;
        background-color: v.$white;
        text-align: left;
    }

    &__autor {
        font-size: 1.2rem;
        margin-bottom: 1rem;
        font-weight: 400;
    }

    &__titulo {
        font-size: 2rem;
        color: v.$darkBlue;
        margin-bottom: 1rem;
        display: inline-block;

        &:hover {
            color: v.$limeGreen;
        }
    }
}