@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer {
    background-color: v.$darkBlue;

    &__c{
        @include m.grid(1, 4rem);
        justify-items: center;

        @include m.desktop {
            display: grid;
            grid-template-columns: 1fr 1fr 2fr;
            gap: 4rem;
            padding-top: 4rem;
            padding-bottom: 4rem;
        }
    }

    &__logo-redes {
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 2rem;

        @include m.desktop {
            justify-content: space-between;
            justify-self: flex-start;
        }
    }

    &__redes {
        display: flex;
        gap: 1rem;
   
        a {
            svg path {
            transition: fill 150ms ease-in-out;
            }
  
            &:hover svg path {
            fill: v.$limeGreen;
            }
        }
    }


    &__enlace-c {
        text-align: center;

        @include m.desktop { 
            @include m.grid(2, 5rem);
            text-align: left;
        }

        ul {
            li {
                margin-bottom: 1.5rem;

                @include m.desktop { 
                    margin-bottom: 0;   
                }
            }

            &:last-of-type {

                li:last-of-type {
                    margin-bottom: 0;
                }
            }

            @include m.desktop { 

                &:first-of-type,
                &:last-of-type {

                    li {
                        &:not(:last-of-type) {
                            margin-bottom: 1.5rem;
                        }
                    }
                }
                
            }
        }
    }

    &__enlace {
        color: v.$white;
        transition-property: color;
        transition-duration: .3s;

        &:hover {
            color: v.$limeGreen
        }
    }

    &__invite {
        @include m.boton(v.$limeGreen, v.$brightCyan, v.$white);
        display: inline-block;
    }

    &__rights {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;

        @include m.desktop { 
            justify-content: space-between;
            align-items: flex-end;
            justify-self: flex-end;
        }
    }
}

.autor {
    color: v.$white;
    text-align: center;
    font-size: 1.3rem;
    margin-top: 2rem;

    span {
        font-style: italic;
    }
}

