@keyframes updown {
    0% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(-2rem);
    }

    100%{
        transform: translateY(0px);
    }
}


@keyframes imagen-hero {
    0% {
        transform: translateX(-10rem);
        opacity: 0;
    }

    50% {
        transform: translateX(10rem);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}